.plate {
  transform: scale(0.5) translate(-18em, 0);;
}

/* .modal-plate {
  width: 70%;
} */
.plate-clickable {
  border: none;
  background: none;
  cursor: pointer;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ReactModal__Overlay {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ReactModal__Content {
  position: unset !important;
  height: 80%;
  border: 2px solid black !important;
  inset: 20% 5% !important;
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 0;
  outline: none;
  padding: 0 !important;
  margin: 0 1em;
}

.container-plate {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plate-detail {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
}

.ReactModal__Content {
  padding: 0;
}

.aufgetischt-logo-detail {
  width: 100%;
  max-width: 24em;
  margin-top: 2em;
}

.plate-detail-1 {
  position: relative;
  width: 50%;
  border-right: 2px solid black;
  padding-right: 20px;
  text-align: left;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1em 2em;
}

.plate-detail-1-body {
  margin-top: 1em;
}

.plate-detail-1 h2 {
  font-family: 'acumin-pro';
  font-weight: 300;
  font-style: normal;
  font-size: 6vw;
  margin: 0;
}

.plate-detail-1 p {
  font-family: 'acumin-pro';
  font-weight: 300;
  font-style: normal;
  text-align: center;
  width: 100%;
  font-size: 1.5em;
  margin: 0;
}

.nachschlag {
  position: absolute;
  bottom: 1em;
  right: 1em;
  padding: 0.6em 2em;
  margin: 1em;
  border: 2px solid #000;
  border-radius: 12em;
  background: none;
  outline: none;
  cursor: pointer;
  color: #000;
  font-family: 'acumin-pro';
  font-style: normal;
}

.nachschlag:hover {
  border-color: #FF10F0;
  color: #FF10F0;
}

.nachschlag p {
  font-size: 1.4em;
  font-weight: 400 !important; 
}

.plate-detail-2 {
  position: relative;
  width: 50%;
  height: inherit;
  align-self: center;
  padding: 2em 2em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-text {
  text-align: left !important;
}

.closeModalButton {
  position: absolute;
  top: 0;
  right: 2em;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

.closeIcon {
  font-size: 3.5em;
  font-weight: 100;
}

@media only screen and (max-height: 700px) {
  .plate-detail-1 p:nth-child(2) {
    font-size: 0.35em !important;
    margin-bottom: 3em;
  }
  .nachschlag p {
    font-size: 0.8em !important;
    margin-bottom: 0;
  }
}

@media only screen and (min-width: 2000px) {
  .ReactModal__Content {
    inset: 10% 15% !important;
  }
  .plate-detail-1 h2 {
    width: 100%;
    font-size: 4vw;
  }
}

@media only screen and (max-width: 700px) {
  .plate-detail {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
  }

  .aufgetischt-logo-detail {
    width: 50%;
    margin-top: 0.5em;
  }

  .plate-detail-1-body {
    font-size: 3.2em;
    margin: 0.2em 0;
  }

  .plate-detail-1 {
    width: auto;
    height: 60%;
    border-top: 2px solid black;
    border-right: none;
    /* padding: 0; */
  }

  .plate-detail-1 p {
    width: 100%;
    font-size: 0.35em;
  }

  .plate-detail-2 {
    width: 90%;
    height: 100%;
  }

  .closeModalButton {
    top: -2em;
  }

  .nachschlag {
    margin-right: 0;
    bottom: 0em;
  }

  .nachschlag p {
    font-size: 1em;
  }
}